/* 0. Reset ================================= */
@import 'reset';
@import url('https://fonts.googleapis.com/css?family=Michroma|Spectral');
@import url("https://use.typekit.net/cco7rbw.css");

$tablet-breakpoint: 640px;
$desktop-breakpoint: 1027px;
$edge-breakpoint: 1440px;
$url-font: "nimbus-sans";
$copy-font: "Michroma";


$home-bg-color: #eae6df;
$home-copy-color: #9e8261;
$home-accent-color: #eba900;


@mixin tablet {
	@media only screen and (min-width: $tablet-breakpoint){
		@content;
	}
}

@mixin desktop {
	@media only screen and (min-width: $desktop-breakpoint){
		@content;
	}
}

@mixin bigger-desktop {
	@media only screen and (min-width: $edge-breakpoint){
		@content;
	}
}


/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body {
	background: $home-bg-color;
	width: 100%;
	height: 100%;
	// overflow: hidden;

	&.home-body {
		color: $home-copy-color;
	}
}

.phone-number {
	color: $home-accent-color;
}


/* 2. Content ================================= */

.content {
	width: 100%;
	height: 100vh;
	position: relative;
	font-family: Michroma;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 1.8em;

	.top-section {
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;

		@include desktop {
			flex-direction: row;
			align-items: unset;
			justify-content: space-between;
		}
	}

	.phone-number, .reservations {
		font-family: $url-font;
		font-size: 1.5em;
	}

	.phone-number {
		margin-bottom: 50px;
		
		span {
			padding: 10px;
		}
	}

	.hamburger-menu {
		background: url(../../dist/img/hamburger.png) no-repeat center;
		background-size: auto 100%;
		width: 69px;
		height: 64px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;

		div {
			width: 32px;
			height: 1px;
			background: white;
			margin: 5px 0;

			&:nth-child(2) {
				margin-left: 10px;
			}
		}

		@include desktop {
			display: none;
		}
	}

	.reservations {
		color: $home-copy-color;
		display: none;
		text-decoration: none;

		@include desktop {
			display: block;
		}
		
		// @include bigger-desktop {
		// 	transform:rotate(-90deg);  
		// 	transform-origin: left;
		// 	position: relative;
		// 	left: 15px;
		// 	top: 214px;
		// 	font-size: 2em;
		// 	padding: 0px;
		// 	text-align: left;
		// }
	}

	.middle-group {
		margin: 42px 0;

		// @include tablet {
		// 	padding-top: 1%;
		// }

		@include desktop {
			padding: 0 1em;
			text-align: center;
		}

		// @include bigger-desktop {
		// 	padding-top: 15%;
		// }

		nav {
			list-style: none;
			font-weight: 500;
			font-size: 2em;
			line-height: 1.7;

			// @include tablet {
			// 	font-size: 3em;
			// }
			
			@include desktop {
				font-size: 3em;
				max-width: 1000px;
			}

			.nav-dash {
				display: none;

				@include desktop {
					display: inline;
				}
			}

			li {
				@include desktop {
					display: inline;
				}
			}

			a {
				text-decoration: none;
				text-transform: lowercase;
				color: $home-copy-color;
			}
		}
	}

	.kr-footer {
		width: 85%;
		max-width: 550px;
		font-family: $url-font;
		font-size: 1.5em;
		text-transform: lowercase;

		// @include tablet {
		// 	padding-top: 8%;
		// }

		@include desktop {
			width: 100%;
			max-width: unset;
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-top: auto;
		}

		.social {
			display: flex;
			justify-content: space-between;
			width: 100%;

			@include desktop {
				width: 20%;
			}

			.icon {
				width: 37px;
				height: 32px;

				a {
					color: rgba(0,0,0,0);
				}
			}

			.fb-icon {
				background: url(../../dist/img/fb-icon.png) no-repeat center;
				background-size: auto 100%;
			}

			.ig-icon {
				background: url(../../dist/img/ig-icon.png) no-repeat center;
				background-size: auto 100%;
			}

			.tw-icon {
				background: url(../../dist/img/twitter-icon.png) no-repeat center;
				background-size: auto 100%;
			}
		}

		p {
			display: none;

			@include desktop {
			display: flex;
			flex-direction: column;
			text-align: right;
			}
		}
	}
}